// 노드 카드 기본 스타일
.node-card {
  position: absolute;
  width: 280px;
  min-height: 120px;
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 32px rgba(31, 38, 135, 0.15);
  overflow: visible;
  cursor: move;
  // transition 제거 - 드래그 시 지연 방지

  &:hover {
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.25);
    border-color: rgba(102, 126, 234, 0.5);
  }

  &.executing {
    background: rgba(76, 175, 80, 0.2);
    backdrop-filter: blur(15px);
    border: 2px solid rgba(76, 175, 80, 0.5);
    animation: pulse 1.5s ease-in-out infinite;
  }

  &.start-node {
    background: rgba(102, 126, 234, 0.15);
    backdrop-filter: blur(15px);
    border: 2px solid rgba(102, 126, 234, 0.5);

    .node-header {
      background: rgba(102, 126, 234, 0.2);
      backdrop-filter: blur(10px);
      color: #667eea;
    }

    .type-badge {
      background: rgba(102, 126, 234, 0.3);
      color: #667eea;
    }
  }

  &.end-node {
    background: rgba(245, 87, 108, 0.15);
    backdrop-filter: blur(15px);
    border: 2px solid rgba(245, 87, 108, 0.5);

    .node-header {
      background: rgba(245, 87, 108, 0.2);
      backdrop-filter: blur(10px);
      color: #f5576c;
    }

    .type-badge {
      background: rgba(245, 87, 108, 0.3);
      color: #f5576c;
    }
  }
}

.connector {
  position: absolute;
  width: 60px;
  height: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 200;
  transition: all 0.2s ease;

  &.left {
    left: -30px;
    top: 50%;
    transform: translateY(-50%);
    cursor: crosshair; // 드롭 타겟 표시
  }

  &.right {
    right: -30px;
    top: 50%;
    transform: translateY(-50%);
    cursor: grab; // 드래그 가능 표시

    &:active {
      cursor: grabbing; // 드래그 중
    }
  }

  &:hover {
    .connector-dot {
      width: 32px;
      height: 32px;
      background: rgba(118, 75, 162, 0.95);
      backdrop-filter: blur(10px);
      box-shadow: 0 0 24px rgba(118, 75, 162, 0.8), 0 0 0 6px rgba(118, 75, 162, 0.3);
      border-width: 5px;
    }

    .connector-label {
      opacity: 1;
      transform: translateY(0);
    }

    .connector-hint {
      opacity: 1;
    }
  }
}

.connector-dot {
  width: 24px;
  height: 24px;
  background: rgba(102, 126, 234, 0.9);
  backdrop-filter: blur(5px);
  border: 4px solid rgba(255, 255, 255, 0.95);
  border-radius: 50%;
  transition: all 0.2s ease;
  box-shadow: 0 4px 16px rgba(102, 126, 234, 0.5), 0 0 0 4px rgba(102, 126, 234, 0.2);
}

.connector-label {
  position: absolute;
  bottom: -28px;
  font-size: 12px;
  font-weight: 700;
  color: #667eea;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(5px);
  padding: 4px 10px;
  border-radius: 6px;
  opacity: 0;
  transform: translateY(-5px);
  transition: all 0.2s ease;
  white-space: nowrap;
}

.connector-hint {
  position: absolute;
  top: -38px;
  font-size: 13px;
  font-weight: 600;
  color: rgba(102, 126, 234, 0.95);
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(15px);
  padding: 8px 14px;
  border-radius: 8px;
  opacity: 0;
  transition: all 0.3s ease;
  white-space: nowrap;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.25);
  border: 1px solid rgba(102, 126, 234, 0.2);
  pointer-events: none;
}

.start-hint {
  font-size: 9px;
  font-weight: 600;
  color: white;
  background: rgba(102, 126, 234, 0.9);
  backdrop-filter: blur(5px);
  padding: 4px 10px;
  border-radius: 6px;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.node-header {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 14px 16px;
  background: rgba(102, 126, 234, 0.1);
  backdrop-filter: blur(10px);
  color: #667eea;
  border-radius: 16px 16px 0 0;
  overflow: visible;
}

.node-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  background: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(5px);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.5);

  mat-icon {
    font-size: 28px;
    width: 28px;
    height: 28px;
    line-height: 28px;
  }
}

.node-title {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 6px;
  min-width: 0;
}

.type-badge {
  display: inline-block;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  background: rgba(102, 126, 234, 0.2);
  backdrop-filter: blur(5px);
  padding: 5px 12px;
  border-radius: 6px;
  width: fit-content;
  border: 1px solid rgba(102, 126, 234, 0.2);
}

.node-name {
  font-size: 16px;
  font-weight: 600;
  opacity: 0.9;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.edit-btn {
  width: 40px;
  height: 40px;
  padding: 0;
  min-width: 40px;
  background: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(5px);
  color: #667eea;
  border: 1px solid rgba(255, 255, 255, 0.5);

  &:hover {
    background: rgba(255, 255, 255, 0.6);
  }

  mat-icon {
    font-size: 22px;
    width: 22px;
    height: 22px;
    line-height: 22px;
  }
}

.node-body {
  padding: 20px;
  color: #333;
  font-size: 15px;
  line-height: 1.7;
  min-height: 70px;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(5px);
  border-radius: 0 0 16px 16px;
  overflow: visible;

  .start-node &,
  .end-node & {
    text-align: center;
    font-weight: 500;
  }
}

.node-hint {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 14px 18px;
  background: rgba(102, 126, 234, 0.08);
  backdrop-filter: blur(5px);
  border-top: 1px solid rgba(102, 126, 234, 0.1);
  font-size: 13px;
  color: #667eea;
  font-weight: 600;
  animation: fadeInUp 0.5s ease;

  mat-icon {
    font-size: 16px;
    width: 16px;
    height: 16px;
  }

  .start-node & {
    background: rgba(102, 126, 234, 0.15);
    border-top-color: rgba(102, 126, 234, 0.2);
  }
}

@keyframes pulse {
  0%, 100% {
    box-shadow: 0 8px 32px 0 rgba(76, 175, 80, 0.3);
  }
  50% {
    box-shadow: 0 8px 32px 0 rgba(76, 175, 80, 0.5);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// 메뉴 스타일
::ng-deep {
  .mat-mdc-menu-panel {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    border-radius: 12px;
    box-shadow: 0 8px 32px rgba(31, 38, 135, 0.2);
    overflow: hidden;
    border: 1px solid rgba(255, 255, 255, 0.3);
  }

  .mat-mdc-menu-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px 20px;
    font-size: 14px;

    mat-icon {
      color: #667eea;
      font-size: 20px;
      width: 20px;
      height: 20px;
    }

    &:hover {
      background: rgba(102, 126, 234, 0.1);
    }
  }
}
